.bg-dark-v2 { background-color: #232F3E; }

.box-shadow { box-shadow: 0 .25rem .75rem rgba(0, 0, 0, .05); }
.box-shadow-v2 { box-shadow: 0 2px 5px rgba(0, 0, 0, 0.14); }
.box-shadow-v2:hover{-webkit-transition:all .2s ease-in-out; transition:all .2s ease-in-out; box-shadow:0 5px 10px rgba(0,0,0,.14)}

.card-v2 { border: 0; }
.card-v2 .card-text { margin-bottom: 0; }
.card-v2 .recharts-wrapper { margin-top: 1rem; }

.content-center { display: flex; justify-content: center; }

.fa { color: #555; }
.highlight-color { color: $main-color; }

.power-value { color: $blue-color; }
.temp-value { color: $red-color; }
