.alert { width: 100%; }

.breadcrumb { background-color: transparent; }
.breadcrumb-item.active { color: inherit; }
.breadcrumb-item a { color: $main-color; }

.btn { font-size: 0.9rem; font-weight: bold; }

.btn-primary {
  background-color: $main-color;
  border-color: $main-color;
}
.btn-primary:hover {
  background-color: $main-color-hover;
  border-color: $main-color-hover;
}
.btn-primary:focus,
.btn-primary.focus {
  background-color: $main-color-hover;
  border-color: $main-color-hover;
  box-shadow: 0 0 0 0.2rem rgba($main-color-hover, 0.3);
}
.btn-primary:not(:disabled):not(.disabled):active,
.btn-primary:not(:disabled):not(.disabled).active,
.show > .btn-primary.dropdown-toggle {
  color: #fff;
  background-color: $main-color-hover;
  border-color: $main-color-hover;
}
.btn-primary:not(:disabled):not(.disabled):active:focus,
.btn-primary:not(:disabled):not(.disabled).active:focus,
.show > .btn-primary.dropdown-toggle:focus {
  box-shadow: 0 0 0 0.2rem rgba($main-color-hover, 0.3);
}
.btn-primary.disabled,
.btn-primary:disabled {
  background-color:$main-color;
  border-color: $main-color;
}

.btn-outline-primary {
  color: $main-color;
  border-color: $main-color;
}
.btn-outline-primary:hover {
  background-color: $main-color;
  border-color: $main-color;
}
.btn-outline-primary:focus,
.btn-outline-primary.focus {
  color: #fff;
  background-color: $main-color;
  border-color: $main-color;
  box-shadow: 0 0 0 0.2rem rgba($main-color-hover, 0.3);
}
.btn-outline-primary:not(:disabled):not(.disabled):active,
.btn-outline-primary:not(:disabled):not(.disabled).active,
.show > .btn-outline-primary.dropdown-toggle {
  color: #fff;
  background-color: $main-color;
  border-color: $main-color;
}
.btn-outline-primary:not(:disabled):not(.disabled):active:focus,
.btn-outline-primary:not(:disabled):not(.disabled).active:focus,
.show > .btn-outline-primary.dropdown-toggle:focus {
  box-shadow: 0 0 0 0.2rem rgba($main-color-hover, 0.3);
}

.card { height: 100%; width: 100%; border: 0; }
.card-title { color: $main-color; font-size: 1.25rem; font-weight: 100; border-bottom: solid 1px rgba(0, 0, 0, 0.125); }
.card-body { padding: 1rem; }
.card-text { margin-bottom: 0.4rem; }
.card-img { width: auto; height: auto; display: block; margin-left: auto; margin-right: auto; }

.custom-control-input:checked ~ .custom-control-label::before {
  border-color: $main-color;
  background-color: $main-color;
}

.dropdown-item.active, .dropdown-item:active {
  background-color: $main-color;
}

.form-control { color: $text-color; font-size: 0.9rem; }

.jumbotron { width: 100%; border-radius: 0; }

.list-group-item:first-child { border-radius: 0; }
.list-group-item:last-child { border-radius: 0; }

.page-link {
  color: $main-color;
}
.page-link:hover {
  color: $main-color-hover;
}
.page-item.active .page-link {
  background-color: $main-color;
  border-color: $main-color;
}

.table { text-align: center; }
.table th { color: $main-color; font-weight: 400; border-top: none; }
.table tbody { color: $text-color; }
.table-striped tbody tr:nth-of-type(2n+1) { background-color: #fbfbfb; }
.table .form-check-input { position: inherit; margin: 0; }

.text-primary { color: $main-color !important; }

.w-15 { width: 15% !important; }
.w-20 { width: 20% !important; }
