@import '../../../scss/common.scss';

.multi-step-nav {
    list-style-type: none;
    padding: 0;
    text-align: center;
}
.multi-step-nav li {
    border-bottom: 4px solid #ccc;
    display: inline-block;
    line-height: 2.5rem;
}
.multi-step-nav li.active {
    border-bottom-color: $main-color;
}
.multi-step-nav span {
    padding: 0 1.5rem;
}
.multi-step-nav li.active span {
    color: $main-color;
}
